body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #EFEFEF; }

.viewport {
  max-width: 960px;
  margin: auto; }

#navbar {
  background-color: white;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 0 0 rgba(16, 22, 26, 0), 0 1px 1px rgba(16, 22, 26, 0.2);
  padding: 4px 0px; }
  #navbar .viewport {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    #navbar .viewport > div {
      margin: 4px 0px; }
      #navbar .viewport > div:first-child {
        flex-grow: 1; }
      #navbar .viewport > div:last-child {
        flex-grow: 1; }
  #navbar #navbar-title {
    font-size: 20px;
    font-weight: 600;
    text-align: left; }
  #navbar .bp3-input {
    width: 320px; }
  #navbar #navbar-right {
    text-align: right; }

#favorites {
  margin-top: 16px;
  padding: 4px 4px 0px;
  text-align: left;
  overflow: auto;
  border: 1px dashed #D0D0D0;
  border-radius: 3px; }
  #favorites .bp3-tag {
    margin-right: 4px;
    margin-bottom: 4px; }
  #favorites #favorites-controls {
    float: right; }
    #favorites #favorites-controls:hover {
      cursor: pointer; }

main.viewport .bp3-card {
  margin-top: 16px;
  text-align: left; }

.flex-card-section {
  display: flex;
  flex-wrap: wrap;
  padding: 12px; }
  .flex-card-section div {
    flex-grow: 1; }

.rv-xy-plot__series--label-text {
  text-shadow: 1px 1px 3px white; }

#user-summary {
  overflow: auto; }
  #user-summary #avatar {
    float: left; }
    #user-summary #avatar img {
      max-width: 100px;
      max-height: 100px;
      margin-right: 16px; }
  #user-summary #favorite {
    float: right; }
    #user-summary #favorite:hover {
      cursor: pointer; }
  #user-summary .bp3-icon {
    margin-left: 8px;
    margin-right: 4px; }
    #user-summary .bp3-icon:first-child {
      margin-left: 0px; }
  #user-summary .bp3-tag {
    margin-right: 4px; }

#repository-list {
  margin-bottom: 42px; }
  #repository-list #repository-list-controls {
    float: right; }
  #repository-list #repository-list-cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  #repository-list #repository-list-table-container {
    overflow: auto; }

.repository {
  flex-grow: 1;
  margin: 8px;
  background-color: #B3CFFF;
  border-radius: 3px; }
  .repository > div {
    padding: 8px; }
  .repository .repository-header {
    color: white;
    background-color: #4B6AB2;
    border-radius: 3px 2px 0px 0px; }
    .repository .repository-header a:link {
      color: white; }
    .repository .repository-header a:visited {
      color: white; }
    .repository .repository-header .bp3-icon {
      margin-right: 8px; }
  .repository .repository-tag {
    float: right;
    padding: 0px 4px;
    margin-left: 8px;
    color: #1F4B99;
    background-color: white;
    border-radius: 2px; }
  .repository .repository-footer {
    overflow: auto; }
    .repository .repository-footer .bp3-icon {
      margin-left: 8px;
      margin-right: 4px; }
      .repository .repository-footer .bp3-icon:first-child {
        margin-left: 0px; }
    .repository .repository-footer .repository-footer-right {
      float: right; }

